.photo{display:flex;justify-content:center;align-items:center;height:50%;background:#073b4c;background-size:cover;background-repeat:no-repeat;background-position:center;color:#111;border:5px solid;outline:5px #111;width:50%;outline:none}.photo:nth-child(-n+2){border-left:10px solid}.photo:nth-last-child(-n+2){border-right:10px solid}.photo:nth-child(6n-5),.photo:nth-child(6n-3),.photo:nth-child(6n-1){border-top:10px solid}.photo:nth-child(6n-4),.photo:nth-child(6n-2),.photo:nth-child(6n){border-bottom:10px solid}.photo:nth-child(6n-5){height:25%;width:auto;width:initial}.photo:nth-child(6n-4){height:75%;width:auto;width:initial}.photo:nth-child(6n-3),.photo:nth-child(6n-2){height:50%;width:auto;width:initial}.photo:nth-child(6n-1){height:70%;width:auto;width:initial}.photo:nth-child(6n){height:30%;width:auto;width:initial}
